<template>
  <div :class="{'pa-file': true, 'pa-file--hover': hover}"
       @click="$refs.input.click()"
       @drop.prevent="onDrop"
       @dragover.prevent="hover = true"
       @dragleave.prevent="hover = false">
    {{text}}
    <input type="file" ref="input" @change="onChange"/>
  </div>
</template>

<script>
  export default {
    name: "VueFilesDropper",
    props: {
      placeholder: String,
      value:{}
    },
    data: ()=>({
      hover: false,
    }),
    watch: {
      value(file){
        //console.log(file);
      }
    },
    computed: {
      text() {
        return "drop-your-file"
      }
    },
    methods:{
      onChange(e){
        this.$emit('input', e.target.files[0]);
      },
      onDrop(e){
        this.$emit('input', e.dataTransfer.files[0]);
        this.hover = false;
      }
    }
  }
</script>

